// src/router.js
import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from './views/Home.vue'; // 正确导入 Home 组件

Vue.use(VueRouter);

const routes = [
  { path: '/', component: Home, meta: {title: '欢迎来到希望的世界'} }, // Home 是主页面的组件
  { path: '/main', component: () => import('./views/Main.vue') , meta: {title: '那些人，那些事'} },
  { path: '/latterns-sky', component: () => import('./views/LatternsSky.vue') , meta: {title: '漫天星光'} },
  { path: '/latterns/:id', component: () => import('./views/LatternsRead.vue'), props: true , meta: {title: '那些希望'} },
  { path: '/fly-latterns', component: () => import('./views/LatternsFly.vue') , meta: {title: '放飞梦想'} },
  { path: '/regist', component: () => import('./views/Regist.vue') , meta: {title: '注册梦乡'} },
  { path: '/login', component: () => import('./views/Login.vue') , meta: {title: '登录梦乡'} },
  { path: '/user', component: () => import('./views/User.vue') , meta: {title: '我的梦乡'} },
];

const router = new VueRouter({
  routes,
  mode: 'history', // 使用历史模式路由
});

// 在路由切换前更新页面标题
router.beforeEach((to, from, next) => {
  document.title = to.meta.title || '待飞之愿';
  next();
});

export default router;